<style>
.box{
	width: 300px;
	height: 400px;
	margin: auto;
	transform: rotateX(30deg);
	transform-style: preserve-3d;
}
.box ul{
	width: 300px;
	height: 400px;
	position: relative;
	left: 150px;
	transform-style: preserve-3d;
	
}
.box ul li{
	width: 300px;
	height: 400px;
	position: absolute;
	transform-origin: left;
	transition: all 2s;
	cursor: pointer;
	box-shadow: 0 0 6.18px 6.18px palevioletred;
}
</style>

<template>
	<div class="box" @mousemove="handleMove($event)" @mouseout="handleOut">
		<ul>
			<li v-for="(item,index) in list" :key="index" :style="{'transform':`rotateY(${index*tranDeg}deg)`,'box-shadow':item.bShanow}" @click="showImg(item)">
				<img :src="`../static/img/${item.id}.jpg`" width="100%" height="100%" @mousedown="cannotDrag($event)">
			</li>
		</ul>
	</div>
</template>

<script>
export default {
	name: 'App',
	data() {
		return {
			list: [],
			tranDeg: 0,
			canMove: false,
		}
	},
	computed:{
		
	},
	methods:{
		handleMove(e){
			let len = this.list.length * 10;
			if(this.canMove) {
				this.tranDeg = e.clientX / 10;
				if(this.tranDeg>0){
					this.tranDeg = 0;
				}
			}	
		},

		randomColor(){
			let [r,g,b] = [Math.floor(Math.random()*256),Math.floor(Math.random()*256),Math.floor(Math.random()*256)];
			return `rgb(${r},${g},${b})`
		},

		handleOut() {
			this.tranDeg = -(180/(this.list.length-1));
		},

		cannotDrag(e){
			e.preventDefault();
		},

		showImg(item) {
			
		},
	},
	created() {
		for(let i=1;i<=11;i++){
			this.list.push({
				id:i,
				bShanow:`0 0 6.18px 6.18px ${this.randomColor()}`
			});
		}
	},
	mounted() {
		
	
		
		console.log(this.list);
		setTimeout(()=>{
			this.tranDeg = -(180/(this.list.length-1));
		},240);
		window.addEventListener('mousedown',()=>{
			this.canMove = true;
		});	
		window.addEventListener('mouseup',()=>{
			this.canMove = false;
		});	
	}
}
</script>





